import React from 'react'
import "./Products.scss";
function Products() {
    const products = [
        {
            id:0,
            linkImage:'https://images.squarespace-cdn.com/content/v1/5ce82b63338ade0001773744/1559253805745-IUOL4DLCPMQN7BMAY4OQ/download+%287%29.jpeg?format=750w'
        },
        {
            id:1,
            linkImage:'https://images.squarespace-cdn.com/content/v1/5ce82b63338ade0001773744/1559254414057-BT9LBEFTBQBMJPA9CMVZ/download+%282%29.jpeg?format=750w'
        },
        {
            id:2,
            linkImage:'https://images.squarespace-cdn.com/content/v1/5ce82b63338ade0001773744/1559253861275-XR8S53GVN47EFCIQQ8UQ/download+%286%29.jpeg?format=750w'
        }
    ]
    return (
        <section className="products">
            <h1>Only the Highest Quality Ingredients</h1>
            <p>In fact, we grow most of them ourselves on our 17-acre farm!</p>
            <div className="products__image">
                {products.map(item=>{
                    return  <a href="#image1" key={item.id}>
                    <img src={item.linkImage} alt={`product-${item.id}`}/>
                    </a>
                })}
            </div>
            <button className="products__shopNow">Shop Now</button>
            <img className="products__imageFull" src="https://images.squarespace-cdn.com/content/v1/5ce82b63338ade0001773744/1559153723232-1DM5OCLN1FPII8S0S6I5/pink-image.jpg" alt="bg"/>
        </section>
    )
}

export default Products
