import React, {  useState } from 'react'
import "./Header.scss";
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
function Header() {
    const [hambuger,setHambuger] = useState(false);
    // console.log(hambuger);
    // console.log('windowX',(window.screen.width ));
    // const screenX = window.screen.width;
    // useEffect(()=>{
    //     if(screenX> 950){
    //         setHambuger(false);
    //     }
    //     console.log('hi')
    // },[hambuger])
    return (
        <>
        <header className={`header ${hambuger ? 'header__white' : ''}`}>
            <div className={`${hambuger ? 'header__hambugerWhite' : 'header__hambuger'}`} onClick={()=>setHambuger(!hambuger)} />
            <div className="header__left">
                <ul>
                    <li>
                        <a href="#shop">Shop</a></li>
                    <li>
                        <a href="#our">Our Story</a></li>
                    <li>
                        <a href="#blog">Blog</a></li>
                </ul>
              
              
             
            </div>
            <div className={`header__mid ${hambuger && 'header__white'}`}>
                <a href="#LOGO">Hester</a>
            </div>
            <div className={`header__right ${hambuger && 'header__white'}`}>
                <a href="#login">Login</a>
                <AddShoppingCartIcon/>
            </div>
   
        </header>
          {   hambuger && 
                    <div className="navHambuger">
                        <ul>
                                <li>
                                    <a href="#shop">Shop</a></li>
                                <li>
                                    <a href="#our">Our Story</a></li>
                                <li>
                                    <a href="#blog">Blog</a></li>
                                    <li>
                                    <a href="#blog">Login</a></li>
                            </ul>
                        </div>
         }
     
         </>
    )
}

export default Header
